import React, { useEffect, useState } from 'react'
import { VictoryBar, VictoryChart, VictoryAxis, VictoryTheme, VictoryLabel, VictoryZoomContainer } from 'victory'
import './Chart.css'

interface DataItem {
    city: string;
    donations: number;
    label: number;
}
interface Data {
    [key: string]: number;
}
interface Props {
    data: any;
    setSearch: (c: string) => void,
    loading: boolean
}

const CityChart = ({ data, setSearch, loading }: Props) => {

    let initial_data = [
        { city: 'Delhi NCR', donations: 0, label: 0 },
        { city: 'Agra', donations: 0, label: 0 },
        { city: 'Lucknow', donations: 0, label: 0 },
        { city: 'Jaipur', donations: 0, label: 0 },
        { city: 'Kolkata', donations: 0, label: 0 },
        { city: 'Ranchi', donations: 0, label: 0 },
        { city: 'Odisha', donations: 0, label: 0 },
        { city: 'Varanasi', donations: 0, label: 0 },
        { city: 'Pune', donations: 0, label: 0 },
        { city: 'Kanpur', donations: 0, label: 0 },
        { city: 'Consulting', donations: 0, label: 0 }
    ]

    let response = {
        'Delhi NCR': 0,
        'Agra': 0,
        'Lucknow': 0,
        'Jaipur': 0,
        'Kolkata': 0,
        'Ranchi': 0,
        'Odisha': 0,
        'Varanasi': 0,
        'Pune': 0,
        'Kanpur': 0,
        'Consulting': 0
    }

    const [chartData, setData] = useState<DataItem[]>(initial_data)
    useEffect(() => {
        if (!loading && data) {
            let updateArray = [...chartData]
            console.log('setting the data', updateArray)
            setData(updateArray.map((da: DataItem) => {
                da.donations = data[da.city]
                return da
            }))
            console.log('data updated', chartData)
            const demo = chartData.map((da: DataItem) => {
                da.donations = data[da.city]
                return da
            })
            setData(demo)
            console.log('updated 2', chartData)
        }
    }, [loading, data])

    return (
        <div className="container chart-container my-4">
            <VictoryChart
                theme={VictoryTheme.material}
                width={800}
                height={400}
                domainPadding={100}
                animate={{
                    duration: 2000,
                    onLoad: { duration: 1000 }
                }}
            >
                <VictoryLabel text="Monthly Report" x={225} y={30} textAnchor="middle" />
                <VictoryAxis

                    tickValues={chartData.map(f => f.city)}
                />
                <VictoryAxis
                    dependentAxis

                    tickFormat={x => x}
                />
                <VictoryBar
                    data={chartData}
                    x="city"
                    y="donations"
                    alignment='middle'
                    events={[{
                        target: "data",
                        eventHandlers: {
                            onClick: () => {
                                return [{
                                    target: "data",
                                    mutation: (props) => {
                                        console.log(props.data[props.index].url)
                                        return true
                                    }
                                }];
                            }
                        }
                    }]}
                />
            </VictoryChart>
        </div>
    )
}

export default CityChart
